body{
    background-color: rgba(196, 196, 196, 0.418);
}
.intro-con{
    height: 624px;
    background-color: rgb(16, 20, 24);
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.me-img{
    width: 300px;
    border-radius: 90%;
    margin-left: auto;
    margin-right: auto;
}
.title{ 
    font-family: 'Roboto', sans-serif;
    font-size: 44px;
    padding: 0;
    margin: 0;
    margin-top: 40px;
    padding-top: 30px;
    justify-content: center;
    text-align: center;
    color: silver;
}
.title-name{
    margin-left: 10px;
    font-size: 55px;
    font-family: 'Caveat', cursive;
}
.skills,.projects,.expi,.socials{
    margin-top: 20px;
}
.expi{
    margin-bottom: 20px;
}
.skills>.t,.projects>.t,.expi>.t,.socials>.t{
    font-size: 40px;
    text-align: center;
    font-family: 'Bebas Neue', cursive;
}
.more{
    text-align: center;
    font-family: 'Bebas Neue', cursive;
    width: 80px;
    margin-left: auto;
    margin-right: auto;
    padding: 5px 0;
}
.more:hover{
    cursor: pointer;
    background-color: springgreen;
    border-radius: 90px;
    width: 80px;
}
.stack{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}