@import url('https://fonts.googleapis.com/css?family=VT323');

$font: #00FF41;
$bg: #003B00;


h1 {
  text-align: center;
  font-family: 'VT323', monospace;
  color: $font !important;
}

#cursor {
  border-left: .1em solid $font;
  animation: blink .7s steps(1) infinite;
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}