@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');
.skill{
    margin: 20px;
    margin-top: 10px;
}
.skill>div>.title,.skill>.title{
    color: rgb(13, 141, 163);
    font-family: 'Slabo 27px', serif;
    font-size: 23px;
    padding-top: 0;
    margin-top: 0px;
}
.skill>div{
    display: flex;
   justify-content: center;
}
.skill>div svg{
    margin-top: 7px;
    margin-left: 3px;
}
.skill>div a{
    text-decoration: none;
    color: inherit;
}
.extras{
    display: flex;
    margin-top: 10px;
    flex-wrap: wrap;
}
.addons{
    background-color: rgb(9, 52, 87);
    color: rgb(223, 192, 192);
    border-radius:6px;
    padding: 3px 5px;
    margin: 3px 5px;
    font-size: 16px;
    font-family: 'Roboto Condensed', sans-serif;
}
.addons>a{
    color: inherit;
    text-decoration: none;
}
.post{
    text-align: center;
    margin:0;
    font-family: 'Slabo 27px', serif;
    font-size: 26px;
    color: rgb(27, 99, 112);
}
.contene{
    font-family: 'Roboto', sans-serif;
}
.features{
    list-style: none;
    font-family: 'Roboto', sans-serif;
}
.features> li{
  margin: 10px;
}